<template>
    <div class="container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>首页</el-breadcrumb-item>
        <el-breadcrumb-item>分类列表</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="tools">
          <el-button type="primary" class="addItem" @click="addCategory()">新增分类</el-button>
      </div>
      <div class="tableContainer">
        <el-table
          :data="tableData"
          height="100%"
          border
          style="width: 100%" class="table">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="分类id">
                  <span>{{ props.row.id }}</span>
                </el-form-item>
                <el-form-item label="父分类">
                  <span>{{ props.row.parent_name }}</span>
                </el-form-item>
                <el-form-item label="分类名">
                  <span>{{ props.row.name }}</span>
                </el-form-item>
                <el-form-item label="创建时间">
                  <span style="color: #00a0e9">{{ props.row.create_time }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
            prop="id"
            label="分类id">
          </el-table-column>
          <el-table-column
            prop="parent_name"
            label="父分类">
          </el-table-column>
          <el-table-column
            prop="name"
            label="分类名">
          </el-table-column>
          <el-table-column
            prop="create_time"
            label="创建时间">
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleDelete(scope.row,scope.$index)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalSize">
        </el-pagination>
      </div>
    </div>
</template>

<script>
  const language = {
    'CN': '简体中文',
    'HK': '繁体中文-香港',
    'TW': '繁体中文-台湾',
    'EN': '英语',
    'ID': '印尼语',
    'TH': '泰语',
    'MY': '马来语',
    'UA': '阿拉伯语'
  }
  const degree = ["低","中","高"];
  const statusArr = ['新增','翻译通过','翻译待审核','翻译不通过','翻译进行中'];
  export default {
    name: "submitDemand",
    data() {
        return {
          tableData: [],
          totalSize: 0,
          currentPage: 1,
          pageSize: 100,
          statusValue: '-1',
          degreeValue: '-1',
          statusOptions: [
            {
              value: '-1',
              label: '所有状态'
            },
            {
              value: '0',
              label: '新增'
            }, {
              value: '1',
              label: '翻译通过'
            }, {
              value: '2',
              label: '翻译待审核'
            }, {
              value: '3',
              label: '翻译不通过'
            }, {
              value: '4',
              label: '翻译进行中'
            }
          ],
          degreeOptions: [
            {
              value: '-1',
              label: '所有等级'
            },
            {
              value: '0',
              label: '低'
            }, {
              value: '1',
              label: '中'
            }, {
              value: '2',
              label: '高'
            }
          ]
        }
    },
    methods: {
      degreeChange(val) {
        if(val >= 0){
          if(this.statusValue >= 0){
            this.initTable(val,this.statusValue);
          }else {
            this.initTable(val,null);
          }
        }else {
          if(this.statusValue >= 0){
            this.initTable(null,this.statusValue);
          }else {
            this.initTable();
          }
        }
      },
      statusChange(val) {
        if(val >= 0){
          if(this.degreeValue >= 0){
            this.initTable(this.degreeValue,val);
          }else{
            this.initTable(null,val);
          }
        }else {
          if(this.degreeValue >= 0){
            this.initTable(this.degreeValue,null);
          }else{
            this.initTable();
          }
        }
      },
      filterDegree(value, row, column) {
        const property = column['property'];
        return row[property] === this.degreeOptions[parseInt(value)+1].label;
      },
      filterStatus(value, row, column) {
        const property = column['property'];
        return row[property] === this.statusOptions[parseInt(value)+1].label;
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      addCategory() {
        this.$router.push('/product/category/add')
      },
      handleEdit(row) {
        this.$router.push({ path: '/categoryManage/updatecategory',query: row})
      },
      handleDelete(row,index) {
        let url = '/v1/categories/'+row.id;
        let that = this;
        this.$confirm('是否确认删除该条记录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.$axios.delete(url).then((res) => {
            if(res.data.code == 200){
              that.tableData.splice(index,1);
              that.$message({
                type: 'success',
                message: '删除成功!'
              });
            }else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          }).catch(() => {
            this.$message({
              type: 'error',
              message: '删除失败!'
            });
          })
        }).catch(() => {});
      },
      initTable(level,status) {
        let initList = [];
        let url = "/v1/categories?page="+this.currentPage+"&pagesize="+this.pageSize;
        if(level){
          url += "&level="+level;
        }
        if(status){
          url += "&status="+status;
        }
        let that = this;
        this.$axios.get(url).then((res) => {
          if(res.data.code == 200){

            let categorysList = res.data.data.list;

            that.totalSize = parseInt(res.data.data.page_total);
            console.log(categorysList);
            categorysList.forEach((category) => {
              let tempcategory = {};
              tempcategory.id = category.id;
              tempcategory.name = category.name;
              tempcategory.parent_name = category.parent_name;
              tempcategory.create_time = category.create_time;
              initList.push(tempcategory);
            })
          }
          that.tableData = initList;
        }).catch((err) => {})
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style scoped lang="stylus">
  .container
    padding 30px
    position relative
    .tools
      margin 20px 0
      .selected
        width: 150px
        margin-right: 20px
      .addItem
        float: right
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 120px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



